"use client";

import {
  Box,
  Container,
  Heading,
  Text,
  SimpleGrid,
  VStack,
  Image,
  Icon,
  useColorModeValue,
} from "@chakra-ui/react";
import {
  InfoOutlineIcon,
  QuestionOutlineIcon,
  SettingsIcon,
} from "@chakra-ui/icons";

const aboutItems = [
  {
    title: "What",
    icon: InfoOutlineIcon,
    description:
      "STEMCYCLES is a 501(c)(3) nonprofit dedicated to STEM teaching and training. We provide industry-level knowledge and hands-on experiences to underrepresented and low-resource students from K–12 and beyond, helping them build a strong foundation for college, careers, and life.",
  },
  {
    title: "Why",
    icon: QuestionOutlineIcon,
    description:
      "STEM is one of the fastest-growing and most impactful fields in the world, yet access to high-quality STEM education is far from equal. By removing financial, geographic, and social barriers, we help students who are underrepresented or lack resources see STEM as an achievable and sustainable path for their future.",
  },
  {
    title: "How",
    icon: SettingsIcon,
    description:
      "We combine competitive robotics, student-led cultivation camps, and long-term mentorship to grow both technical and soft skills. Through real-world projects, collaboration, and exposure to industry mentors, students gain confidence, connections, and the tools to thrive—then we repeat the cycle with the next generation.",
  },
];

export default function AboutSection() {
  const sectionBg = useColorModeValue("gray.50", "gray.900");
  const cardBg = useColorModeValue("surface.light", "surface.dark");
  const headingColor = useColorModeValue("text.light", "text.dark");
  const bodyTextColor = useColorModeValue("text.mutedLight", "text.mutedDark");

  return (
    <Box pt={{ base: 0, md: 4 }} pb={{ base: 12, md: 20 }} bg={sectionBg}>
      <Container maxW="container.xl">
        <VStack spacing={12}>
          {/* Section Header */}
          <VStack spacing={4} textAlign="center">
            <Image
              src="/images/logos/stemcycles-main.png"
              alt="STEMCYCLES Logo"
              h={80}
              fallbackSrc="https://via.placeholder.com/200x80?text=STEMCYCLES"
            />
            <Heading as="h2" size="xl" color={headingColor}>
              About STEMCYCLES
            </Heading>
          </VStack>

          {/* Three Columns */}
          <SimpleGrid columns={{ base: 1, md: 3 }} spacing={8} w="100%">
            {aboutItems.map((item) => (
              <VStack
                key={item.title}
                p={6}
                bg={cardBg}
                borderRadius="lg"
                borderWidth="1px"
                borderColor={useColorModeValue(
                  "blackAlpha.100",
                  "whiteAlpha.200"
                )}
                shadow="md"
                spacing={4}
                align="center"
                textAlign="center"
              >
                <Icon as={item.icon} boxSize={10} color="brand.500" />
                <Heading as="h3" size="lg" color="brand.600">
                  {item.title}
                </Heading>
                <Text color={bodyTextColor}>{item.description}</Text>
              </VStack>
            ))}
          </SimpleGrid>
        </VStack>
      </Container>
    </Box>
  );
}
