"use client";

import {
  Box,
  Container,
  Heading,
  Text,
  VStack,
  HStack,
  Image,
  Flex,
  useColorModeValue,
} from "@chakra-ui/react";

const initiatives = [
  {
    name: "Trinity Force",
    lightLogo: "/images/logos/trinity-force-light-mode.png",
    darkLogo: "/images/logos/trinity-force-dark-mode.png",
    description:
      "Trinity Force is our all-girls FIRST Robotics Competition (FRC) team, created to give young women a space to lead, design, and compete at a high technical level. Students take on mechanical design, programming, and strategy—owning decisions on and off the field while building confidence, leadership, and real engineering skills.",
    imageOnLeft: true,
  },
  {
    name: "ByteForce",
    lightLogo: "/images/logos/byte-force-light-mode.png",
    darkLogo: "/images/logos/byte-force-dark-mode.png",
    description:
      "ByteForce is our co-ed FRC team focused on high-level engineering, software development, and competitive strategy. Students learn CAD, mechanical fabrication, and Java programming while integrating sensors, automation, and game analysis to build robots that can compete on some of the biggest stages in STEM.",
    imageOnLeft: false,
  },
  {
    name: "Cultivation Camps",
    logo: "/images/logos/cultivation-camps.png",
    description:
      "Our student-led cultivation camps introduce younger students to STEM through hands-on robotics, coding, and design experiences. From building small robots to exploring 3D printing and mechanical systems, these camps spark curiosity, build foundational skills, and expand access to STEM for underrepresented communities.",
    imageOnLeft: true,
  },
];

function InitiativeRow({
  name,
  logo,
  lightLogo,
  darkLogo,
  description,
  imageOnLeft,
}) {
  const rowBorderColor = useColorModeValue("blackAlpha.100", "whiteAlpha.200");
  const descriptionColor = useColorModeValue(
    "text.mutedLight",
    "text.mutedDark"
  );

  const logoSrc =
    lightLogo && darkLogo ? useColorModeValue(lightLogo, darkLogo) : logo;

  const imageContent = (
    <Box
      flex={{ base: "none", md: 1 }}
      display="flex"
      justifyContent="center"
      alignItems="center"
      p={8}
    >
      <Image
        src={logoSrc}
        alt={`${name} Logo`}
        maxW={{ base: "200px", md: "250px" }}
        fallbackSrc={`https://via.placeholder.com/250x150?text=${name.replace(
          " ",
          "+"
        )}`}
      />
    </Box>
  );

  const textContent = (
    <VStack
      flex={{ base: "none", md: 1 }}
      align={{ base: "center", md: imageOnLeft ? "flex-start" : "flex-end" }}
      textAlign={{ base: "center", md: imageOnLeft ? "left" : "right" }}
      spacing={4}
      p={8}
    >
      <Heading as="h3" size="lg" color="brand.600">
        {name}
      </Heading>
      <Text color={descriptionColor} fontSize="lg">
        {description}
      </Text>
    </VStack>
  );

  return (
    <Flex
      direction={{ base: "column", md: "row" }}
      align="center"
      w="100%"
      py={8}
      borderBottomWidth={1}
      borderBottomColor={rowBorderColor}
      _last={{ borderBottom: "none" }}
    >
      {imageOnLeft ? (
        <>
          {imageContent}
          {textContent}
        </>
      ) : (
        <>
          {textContent}
          {imageContent}
        </>
      )}
    </Flex>
  );
}

export default function InitiativesSection() {
  const sectionBg = useColorModeValue("gray.50", "gray.900");
  const headingColor = useColorModeValue("text.light", "text.dark");
  const subtitleColor = useColorModeValue("text.mutedLight", "text.mutedDark");

  return (
    <Box py={{ base: 12, md: 20 }} bg={sectionBg}>
      <Container maxW="container.xl">
        <VStack spacing={8}>
          {/* Section Header */}
          <VStack spacing={4} textAlign="center" mb={8}>
            <Heading as="h2" size="xl" color={headingColor}>
              Our Initiatives
            </Heading>
            <Text color={subtitleColor} fontSize="lg" maxW="2xl">
              Explore the robotics teams and cultivation camps that bring our
              mission to life and expand access to high-quality STEM education.
            </Text>
          </VStack>

          {/* Initiative Rows */}
          <Box w="100%">
            {initiatives.map((initiative) => (
              <InitiativeRow
                key={initiative.name}
                name={initiative.name}
                logo={initiative.logo}
                lightLogo={initiative.lightLogo}
                darkLogo={initiative.darkLogo}
                description={initiative.description}
                imageOnLeft={initiative.imageOnLeft}
              />
            ))}
          </Box>
        </VStack>
      </Container>
    </Box>
  );
}
